.main-nav {
  display: flex;
  padding: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  .main-nav .main-nav__toggle {
    display: none; }
  .main-nav .main-nav__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    font-weight: 300;
    opacity: 0.7; }
  .main-nav .main-nav__list li:hover {
    color: #0ad9c6; }
  @media (max-width: 1023px) {
    .main-nav {
      position: relative;
      padding: 0; }
      .main-nav .main-nav__toggle {
        display: block;
        position: absolute;
        background: #0ad9c6;
        width: 50px;
        height: 50px;
        right: 0;
        padding: 0;
        border: 0;
        z-index: 5;
        cursor: pointer; }
      .main-nav--closed .main-nav__toggle::before {
        content: "";
        position: absolute;
        top: 16px;
        right: 13px;
        width: 25px;
        height: 1px;
        background-color: #1f1f1f;
        box-shadow: 0 8px 0 0 #1f1f1f, 0 16px 0 0 #1f1f1f; }
      .main-nav--opened .main-nav__toggle::before,
      .main-nav--opened .main-nav__toggle::after {
        content: "";
        position: absolute;
        right: 13px;
        width: 25px;
        height: 1px;
        background-color: rgba(20, 21, 23, 0.2); }
      .main-nav--opened .main-nav__toggle::before {
        transform: rotate(45deg); }
      .main-nav--opened .main-nav__toggle::after {
        transform: rotate(-45deg); }
      .main-nav--opened .main-nav__toggle {
        background: transparent; }
      .main-nav--closed .main-nav__list {
        display: none; }
      .main-nav--opened {
        background-color: #ffffff;
        z-index: 2; }
      .main-nav--opened .main-nav__list {
        flex-direction: column;
        justify-content: end;
        min-height: 100vh;
        color: #48494d;
        margin-top: 70px; }
      .main-nav .main-nav__list li {
        padding: 16px;
        background-color: #f6f6f6;
        margin-top: 30px;
        font-size: 20px;
        font-weight: 400;
        line-height: 28px;
        letter-spacing: 0.6px; }
      .main-nav .main-nav__list a {
        position: relative;
        display: block; }
        .main-nav .main-nav__list a::after {
          content: "";
          position: absolute;
          border-top: 1px solid #48494d;
          border-right: 1px solid #48494d;
          width: 8px;
          height: 8px;
          top: 9px;
          right: 10px;
          transform: rotate(45deg); }
      .main-nav--nojs .main-nav__list {
        min-height: 100%; }
      .main-nav--nojs .main-nav__toggle {
        display: none; } }

